body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; 
  margin: auto;
  width: 70%; 
  margin-left: 400px;
}

.product-item {
  flex-basis: calc(33.33% - 10px);
  margin-bottom: 100px; 
}

.filters-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  position: absolute;
  margin-left: 50px;
}

.filter-item {
  margin-bottom: 20px; 
}

.filter-item select {
  width: 200px; 
  height: 40px; 
  font-size: 18px; 
  border-radius: 10px;
  border: 2px solid green;
  background-color: black;
  color: white;
}


img {
  width: 50%;
  height: 50%; 
  object-fit:cover;
}

a{
  font-weight: bold;
  text-decoration: none;
  color: green;
  font-size: larger;
}